"use client";

import * as React from "react";
import dynamic from "next/dynamic";
import { DashboardLayout } from "@/components/dashboard/layout";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Switch } from "@/components/ui/switch";
import { GripVertical, CheckCircle2, X, Loader2 } from "lucide-react";
import axiosClient from "@/lib/axiosClient";

import {
  DndContext,
  closestCenter,
  PointerSensor,
  useSensor,
  useSensors,
} from "@dnd-kit/core";

import {
  arrayMove,
  SortableContext,
  verticalListSortingStrategy,
  useSortable,
} from "@dnd-kit/sortable";

import { CSS } from "@dnd-kit/utilities";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Button } from "@/components/ui/button";

// Client-only preview
const Perview = dynamic(() => import("@/app/preview/page"), { ssr: false });

/* ----------------------------------------------------
   TOAST COMPONENT
---------------------------------------------------- */
const Toast = ({ message, type = "success", onClose }) => {
  React.useEffect(() => {
    const timer = setTimeout(onClose, 4000);
    return () => clearTimeout(timer);
  }, [onClose]);

  return (
    <div
      className={`fixed bottom-8 left-1/2 -translate-x-1/2 z-50 px-6 py-4 rounded-lg shadow-2xl text-white font-medium flex items-center gap-3 ${
        type === "success" ? "bg-green-600" : "bg-red-600"
      }`}
      style={{ animation: "slideUp 0.3s ease-out" }}
    >
      {type === "success" ? <CheckCircle2 className="h-5 w-5" /> : <X className="h-5 w-5" />}
      {message}
    </div>
  );
};

/* ----------------------------------------------------
   SORTABLE ITEM
---------------------------------------------------- */
function SortableItem({ item, availableSections, onToggle, onRemove }) {
  const { attributes, listeners, setNodeRef, transform, transition } =
    useSortable({ id: item.section });

  const [mounted, setMounted] = React.useState(false);
  React.useEffect(() => setMounted(true), []);

  const style = {
    transform: mounted && transform ? CSS.Transform.toString(transform) : undefined,
    transition: mounted ? transition : undefined,
  };

  const label =
    availableSections.find((s) => s.section === item.section)?.label ||
    item.section;

  return (
    <div
      ref={setNodeRef}
      style={style}
      className="flex items-center justify-between rounded-lg border p-4 bg-white shadow-sm"
    >
      <div className="flex items-center gap-3">
        <GripVertical
          className="h-5 w-5 cursor-grab text-gray-400"
          {...attributes}
          {...listeners}
        />
        <span className="font-medium">{label}</span>
      </div>

      <div className="flex items-center gap-3">
        <Switch checked={item.enabled} onCheckedChange={() => onToggle(item.section)} />
        <Button size="sm" variant="ghost" onClick={() => onRemove(item.section)}>
          Remove
        </Button>
      </div>
    </div>
  );
}

/* ----------------------------------------------------
   MAIN BUILDER COMPONENT
---------------------------------------------------- */
export default function LandingPageBuilder({ templateId, userId }) {
  const [selectValue, setSelectValue] = React.useState("");
  const [isSaving, setIsSaving] = React.useState(false);
  const [isLoading, setIsLoading] = React.useState(true);
  const [toast, setToast] = React.useState(null);

  const [availableSections, setAvailableSections] = React.useState([]);
  const [activeSections, setActiveSections] = React.useState([]);

  const [styles] = React.useState({
    primaryColor: "#ecedf4ff",
    backgroundColor: "#000000",
    textColor: "#a97d38",
    headlineSize: 28,
    paragraphSize: 16,
  });

  const showToast = (message, type = "success") => {
    setToast({ message, type });
  };

  const sensors = useSensors(
    useSensor(PointerSensor, { activationConstraint: { distance: 6 } })
  );

  /* ----------------------------------------------------
     FETCH SECTIONS FROM API
  ---------------------------------------------------- */
  React.useEffect(() => {
    const fetchSections = async () => {
      try {
        setIsLoading(true);

        const response = await axiosClient.get("/landing-page/sections");

        if (response.data.success) {
          const sections = response.data.data.sections || [];

          const transformed = sections.map((sec) => ({
            section: sec.key,
            label: sec.name,
            fields: sec.schema?.fields || [],
            group: sec.group,
            sub_group: sec.sub_group,
          }));

          setAvailableSections(transformed);
          setActiveSections([]); // Start empty  user adds sections
        } else {
          showToast("Failed to load sections", "error");
        }
      } catch (error) {
        console.error("Fetch error:", error);
        showToast(
          error.response?.data?.message || "Failed to load sections",
          "error"
        );
      } finally {
        setIsLoading(false);
      }
    };

    fetchSections();
  }, []);

  /* ----------------------------------------------------
     HANDLERS
  ---------------------------------------------------- */
  const handleAddSection = (sectionKey) => {
    if (!sectionKey) return;
    if (activeSections.some((s) => s.section === sectionKey)) return;

    const template = availableSections.find((s) => s.section === sectionKey);
    if (template) {
      setActiveSections((prev) => [
        ...prev,
        {
          section: template.section,
          enabled: true,
          fields: template.fields,
        },
      ]);
      setSelectValue("");
    }
  };

  const toggleEnabled = (key) =>
    setActiveSections((prev) =>
      prev.map((s) => (s.section === key ? { ...s, enabled: !s.enabled } : s))
    );

  const removeSection = (key) =>
    setActiveSections((prev) => prev.filter((s) => s.section !== key));

  const onDragEnd = ({ active, over }) => {
    if (!over || active.id === over.id) return;

    setActiveSections((prev) => {
      const oldIndex = prev.findIndex((s) => s.section === active.id);
      const newIndex = prev.findIndex((s) => s.section === over.id);
      return arrayMove(prev, oldIndex, newIndex);
    });
  };

  const handleSave = async () => {
    setIsSaving(true);
    try {
      const payload = {
        templateId,
        userId,
        sections: activeSections,
        styles,
      };

      const response = await axiosClient.post("/templates/templateupdate/", payload);

      if (response.data.status === "success" || response.data.success) {
        showToast("Template saved successfully!", "success");
      } else {
        showToast(response.data.message || "Failed to save", "error");
      }
    } catch (error) {
      console.error("Save error:", error);
      showToast(error.response?.data?.message || "Failed to save", "error");
    } finally {
      setIsSaving(false);
    }
  };

  const previewPayload = {
    templateName: "modern",
    sections: activeSections,
    styles,
  };

  /* ----------------------------------------------------
     GROUPED SECTIONS FOR DROPDOWN
  ---------------------------------------------------- */
  const groupedSections = React.useMemo(() => {
    return availableSections.reduce((acc, section) => {
      const group = section.group || "Other";
      const subGroup = section.sub_group || "null";

      if (!acc[group]) acc[group] = {};
      if (!acc[group][subGroup]) acc[group][subGroup] = [];

      acc[group][subGroup].push(section);
      return acc;
    }, {});
  }, [availableSections]);

  const hasAvailableToAdd = availableSections.some(
    (s) => !activeSections.some((a) => a.section === s.section)
  );

  if (isLoading) {
    return (
      <DashboardLayout>
        <div className="flex items-center justify-center h-screen">
          <div className="text-center space-y-4">
            <Loader2 className="h-12 w-12 animate-spin mx-auto text-primary" />
            <p className="text-lg text-muted-foreground">Loading sections...</p>
          </div>
        </div>
      </DashboardLayout>
    );
  }

  return (
    <DashboardLayout>
      <div className="max-w-7xl mx-auto space-y-8 p-6">
        <div className="flex items-center justify-between">
          <h1 className="text-3xl font-bold">Landing Page Builder</h1>
          <Button onClick={handleSave} disabled={isSaving} size="lg">
            {isSaving ? "Saving..." : "Save & Publish"}
          </Button>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
          {/* LEFT: Controls */}
          <div className="space-y-6">
            {/* Add Sections */}
            <Card>
              <CardHeader>
                <CardTitle>Add Sections</CardTitle>
                <CardDescription>Choose a section to add</CardDescription>
              </CardHeader>
              <CardContent>
                <Select value={selectValue} onValueChange={handleAddSection}>
                  <SelectTrigger>
                    <SelectValue placeholder="Select a section..." />
                  </SelectTrigger>
                  <SelectContent className="max-h-96">
                    {hasAvailableToAdd ? (
                      Object.entries(groupedSections).map(([group, subGroups]) => (
                        <div key={group}>
                          <div className="px-2 py-1.5 text-sm font-semibold text-muted-foreground uppercase tracking-wider">
                            {group}
                          </div>
                          {Object.entries(subGroups).map(([subGroup, secs]) => {
                            const subLabel = subGroup === "null" ? null : subGroup;
                            const availableSecs = secs.filter(
                              (s) => !activeSections.some((a) => a.section === s.section)
                            );

                            if (availableSecs.length === 0) return null;

                            return (
                              <div key={subGroup}>
                                {subLabel && (
                                  <div className="px-4 py-1 text-xs font-medium text-muted-foreground">
                                    {subLabel.charAt(0).toUpperCase() + subLabel.slice(1)}
                                  </div>
                                )}
                                {availableSecs.map((s) => (
                                  <SelectItem key={s.section} value={s.section}>
                                    {s.label}
                                  </SelectItem>
                                ))}
                              </div>
                            );
                          })}
                        </div>
                      ))
                    ) : (
                      <div className="p-4 text-center text-sm text-muted-foreground">
                        All sections have been added
                      </div>
                    )}
                  </SelectContent>
                </Select>
              </CardContent>
            </Card>

            {/* Reorder Sections */}
            <Card>
              <CardHeader>
                <CardTitle>Page Sections</CardTitle>
                <CardDescription>Drag to reorder  Toggle visibility  Remove</CardDescription>
              </CardHeader>
              <CardContent>
                {activeSections.length > 0 ? (
                  <DndContext
                    sensors={sensors}
                    collisionDetection={closestCenter}
                    onDragEnd={onDragEnd}
                  >
                    <SortableContext
                      items={activeSections.map((s) => s.section)}
                      strategy={verticalListSortingStrategy}
                    >
                      <div className="space-y-3">
                        {activeSections.map((item) => (
                          <SortableItem
                            key={item.section}
                            item={item}
                            availableSections={availableSections}
                            onToggle={toggleEnabled}
                            onRemove={removeSection}
                          />
                        ))}
                      </div>
                    </SortableContext>
                  </DndContext>
                ) : (
                  <div className="text-center py-8 text-muted-foreground">
                    No sections added yet. Use the dropdown above to add sections.
                  </div>
                )}
              </CardContent>
            </Card>
          </div>

          {/* RIGHT: Preview */}
          <Card>
            <CardHeader>
              <CardTitle>Mobile Preview</CardTitle>
            </CardHeader>
            <CardContent className="flex justify-center py-8">
              <div className="w-[360px] h-[720px] rounded-[45px] border-[14px] border-black bg-black overflow-hidden relative shadow-xl">
                <div className="absolute top-0 left-1/2 -translate-x-1/2 w-40 h-6 bg-black rounded-b-3xl z-20"></div>
                <div className="bg-white w-full h-full overflow-y-auto">
                  <Perview data={previewPayload} />
                </div>
              </div>
            </CardContent>
          </Card>
        </div>

        {/* Toast */}
        {toast && (
          <Toast
            message={toast.message}
            type={toast.type}
            onClose={() => setToast(null)}
          />
        )}
      </div>
    </DashboardLayout>
  );
}